import React, { Component } from "react";
import { MdClose } from "react-icons/md";
import { DPMembershipApplicationGetInterface } from "../../actions/dp_membership_application";
import { search } from "../../utils/functions";

interface SelectDevelopmentPartnersProps {
  data: DPMembershipApplicationGetInterface[];
  selected: DPMembershipApplicationGetInterface | null;
  onSelectDp: (item: DPMembershipApplicationGetInterface) => void;
  onClose: () => void;
}
interface SelectDevelopmentPartnersState {
  loading: boolean;
  searchData: string;
}

export class SelectDevelopmentPartners extends Component<
  SelectDevelopmentPartnersProps,
  SelectDevelopmentPartnersState
> {
  constructor(props: SelectDevelopmentPartnersProps) {
    super(props);

    this.state = {
      loading: false,
      searchData: "",
    };
  }
  render() {
    return (
      <div>
        <div className="m-2 md:m-6 bg-white p-2 border border-gray-200 rounded-md shadow-xl animate__animated animate__zoomIn">
          <div className="flex flex-row items-center justify-between gap-2 w-full mb-2">
            <div className="text-lg font-bold mx-1 mb-1">
              Choose Training provider
            </div>
            <div>
              <div
                onClick={this.props.onClose}
                className="bg-accent-400 text-accent-900 rounded-full cursor-pointer h-8 w-8 flex items-center justify-center hover:bg-accent-900 hover:text-white"
              >
                <MdClose className="text-3xl" />
              </div>
            </div>
          </div>
          <div>
            <input
              type="text"
              className="px-3 py-2 rounded border border-gray-400 w-full"
              value={this.state.searchData}
              onChange={(e) => this.setState({ searchData: e.target.value })}
            />
          </div>
          {/* List here */}
          <div>
            {search(this.props.data, this.state.searchData).length === 0 ? (
              <div className="text-xl font-light py-3 px-2">
                No result found!
              </div>
            ) : (
              <div>
                <table className="w-full text-left text-sm mt-2">
                  <thead className="">
                    <tr>
                      <th className="px-3 py-2">#</th>
                      <th className="px-3 py-2">Provider name</th>
                      <th className="px-3 py-2">Category</th>
                      <th className="px-3 py-2">Phone number</th>
                      <th className="px-3 py-2">Email</th>
                      <th className="px-3 py-2">Location</th>
                      <th className="px-3 py-2"></th>
                    </tr>
                  </thead>
                  <tbody>
                    {(
                      search(
                        this.props.data,
                        this.state.searchData
                      ) as DPMembershipApplicationGetInterface[]
                    ).map((item, i) => (
                      <tr
                        key={i + 1}
                        className={`${
                          this.props.selected?.id === item.id
                            ? "bg-accent-400 text-accent-900"
                            : i % 2 !== 0
                            ? "bg-white"
                            : ""
                        } group cursor-pointer hover:bg-accent-400`}
                        onClick={() => this.props.onSelectDp(item)}
                      >
                        <td className="px-3 py-2 border border-gray-300">
                          {i + 1}
                        </td>
                        <td className="px-3 py-2 border border-gray-300">
                          {item.development_partner_name}
                        </td>
                        <td className="px-3 py-2 border border-gray-300">
                          {item.organization_category}
                        </td>
                        <td className="px-3 py-2 border border-gray-300">
                          {item.phone}
                        </td>
                        <td className="px-3 py-2 border border-gray-300">
                          {item.email}
                        </td>
                        <td className="px-3 py-2 border border-gray-300">
                          {item.location}
                        </td>
                        <td className="border border-gray-300 px-2 w-20">
                          <div className="bg-accent-400 group-hover:bg-accent-900 text-center w-full px-2 py-1 rounded text-accent-900 float-right group-hover:text-white cursor-pointer">
                            Select
                          </div>
                        </td>
                      </tr>
                    ))}
                  </tbody>
                </table>
              </div>
            )}
          </div>
        </div>
      </div>
    );
  }
}
