import React, { Component } from "react";
import { IoMdList, IoMdPersonAdd } from "react-icons/io";
import { MdClose, MdKeyboardArrowDown } from "react-icons/md";
import { connect } from "react-redux";
import { Basic, FC_GetAllSystemRoles } from "../../actions";
import {
  DPAccountInterface,
  DPMembershipApplicationGetInterface,
  DPMembershipApplicationStatus,
  GetAllDPListByStatusInterface,
  GetDPAccountsList,
  GetDPListByStatus,
} from "../../actions/dp_membership_application";
import Button from "../../components/Buttons/Button";
import Loading from "../../components/Loading/Loading";
import { StoreState } from "../../reducers";
import { CreateAccount } from "./CreateAccount";
import { DPAccountsList } from "./DPAccountsList";
import { SelectDevelopmentPartners } from "./SelectDevelopmentPartners";

interface DPAccountProps {
  basic: Basic;
  FC_GetAllSystemRoles: (
    callBack: (status: boolean, msg: string) => void
  ) => void;
}
interface DPAccountState {
  loading: boolean;
  action: "create" | "list";
  developmentPartners: GetAllDPListByStatusInterface | null;
  error: string;
  success: string;
  selected_development_partner: DPMembershipApplicationGetInterface | null;
  open_select_development_partner: boolean;
  accounts: DPAccountInterface[] | null;
}

class _DPAccount extends Component<DPAccountProps, DPAccountState> {
  constructor(props: DPAccountProps) {
    super(props);

    this.state = {
      loading: false,
      action: "list",
      developmentPartners: null,
      error: "",
      success: "",
      selected_development_partner: null,
      open_select_development_partner: false,
      accounts: null,
    };
  }
  // Get DP Applications
  FC_GetDPApplications = () => {
    this.setState({
      loading: true,
      error: "",
      success: "",
    });
    const status = DPMembershipApplicationStatus.APPROVED;
    GetDPListByStatus(
      status,
      (
        loading: boolean,
        res: {
          type: "success" | "error";
          msg: string;
          data: GetAllDPListByStatusInterface | null;
        } | null
      ) => {
        this.setState({ loading: loading });
        if (res !== null && res.type === "success") {
          this.setState({
            developmentPartners: res.data,
            error: "",
            success: res.msg,
            loading: false,
          });
        }
        if (res !== null && res.type === "error") {
          this.setState({
            developmentPartners: null,
            success: "",
            error: res.msg,
            loading: false,
          });
        }
      }
    );
  };

  // Get DP Accounts
  FC_GetAccounts = (selected_development_partner: string) => {
    this.setState({ loading: true });
    GetDPAccountsList(
      selected_development_partner,
      (
        loading: boolean,
        res: {
          status: "success" | "error";
          msg: string;
          data: DPAccountInterface[] | null;
        } | null
      ) => {
        this.setState({ loading: loading });
        if (res !== null && res.status === "success") {
          this.setState({
            accounts: res.data,
            loading: false,
            error: "",
            success: "",
          });
        }
        if (res !== null && res.status === "error") {
          this.setState({
            accounts: [],
            loading: false,
            error: res.msg,
            success: "",
          });
        }
      }
    );
  };

  componentDidMount = () => {
    if (
      this.state.developmentPartners === null ||
      this.state.developmentPartners.dp_application.length === 0
    ) {
      this.FC_GetDPApplications();
    }
  };

  render() {
    return (
      <div className="container mx-auto p-3">
        <div className="bg-white rounded p-2 relative">
          <div className="grid grid-cols-12 gap-3 lg:gap-0">
            <div className="col-span-12 lg:col-span-4">
              <div className="text-xl font-bold ml-1">
                {"Training providers accounts"}
              </div>
              <div className="-mt-1 text-sm text-gray-500 ml-1">
                List of all accounts grouped in their training providers
              </div>
            </div>
            <div className="col-span-12 lg:col-span-4">
              <div
                className={`flex flex-row items-center justify-between border ${
                  this.state.selected_development_partner === null
                    ? "border-accent-800 bg-accent-400"
                    : "border-gray-400 font-bold"
                }  px-3 py-2 rounded cursor-pointer hover:bg-accent-400 hover:border-accent-800 hover:text-accent-900`}
                onClick={() =>
                  this.setState({
                    open_select_development_partner:
                      !this.state.open_select_development_partner,
                  })
                }
              >
                <span className="truncate">
                  {this.state.selected_development_partner === null
                    ? "Choose training provider"
                    : this.state.selected_development_partner
                        .development_partner_name}
                </span>
                <div>
                  <MdKeyboardArrowDown className="text-xl" />
                </div>
              </div>
            </div>
            <div className="col-span-12 lg:col-span-4 flex flex-row justify-end gap-2">
              <div
                onClick={() => {
                  this.state.loading === false &&
                    this.setState({ action: "list" });
                }}
                className={`px-2 py-2 pr-4 rounded w-max flex flex-row items-center gap-2 ${
                  this.state.action === "list"
                    ? "bg-accent-400 text-accent-900 font-bold"
                    : "bg-gray-200 hover:bg-accent-400 hover:text-accent-900"
                } cursor-pointer h-10`}
              >
                <div>
                  <IoMdList className="text-xl" />
                </div>
                <span className={``}>View list</span>
              </div>
              <div
                onClick={() => {
                  this.state.loading === false &&
                    this.setState({ action: "create" });
                }}
                className={`px-2 py-2 pr-4 rounded w-max flex flex-row items-center gap-2 ${
                  this.state.action === "create"
                    ? "bg-accent-400 text-accent-900 font-bold"
                    : "bg-gray-200 hover:bg-accent-400 hover:text-accent-900"
                } cursor-pointer h-10`}
              >
                <div>
                  <IoMdPersonAdd className="text-xl" />
                </div>
                <span className={``}>Create account</span>
              </div>
            </div>
          </div>
          {/* Choose DP */}
          {this.state.open_select_development_partner === true &&
            this.state.developmentPartners !== null && (
              <div className="absolute right-0 left-0" style={{ zIndex: 9 }}>
                <SelectDevelopmentPartners
                  data={this.state.developmentPartners.dp_application}
                  selected={this.state.selected_development_partner}
                  onSelectDp={(item: DPMembershipApplicationGetInterface) => {
                    this.setState({
                      selected_development_partner: item,
                      open_select_development_partner: false,
                      accounts: null,
                    });
                    if (this.state.action === "list") {
                      this.FC_GetAccounts(item.id);
                    }
                  }}
                  onClose={() =>
                    this.setState({ open_select_development_partner: false })
                  }
                />
              </div>
            )}
        </div>
        {this.state.error !== "" && (
          <div
            onClick={() => this.setState({ error: "", success: "" })}
            className="bg-red-100 text-red-600 font-bold w-full px-3 py-2 rounded-md cursor-pointer hover:bg-red-200 border-2 border-white my-2 animate__animated animate__shakeX flex flex-row items-center justify-between gap-2"
          >
            <div>{this.state.error}</div>
            <div>
              <MdClose className="text-2xl" />
            </div>
          </div>
        )}
        <div className="mt-3">
          {this.state.loading === true ? (
            <div className="bg-white p-8 rounded-md flex flex-col items-center justify-center">
              <Loading />
              <div className="text-center text-xl font-light">
                Loading, please wait...
              </div>
            </div>
          ) : (
            <div>
              {this.state.selected_development_partner === null && (
                <div className="bg-white rounded-md my-3 p-4">
                  <div className="flex flex-col items-center justify-center">
                    {/* <div>
                      <BsInfoCircleFill className="text-6xl text-gray-400" />
                    </div> */}
                    <div className="text-xl font-bold mt-2">
                      Training provider
                    </div>
                    <div className="text-sm text-gray-600">
                      Click the following button to choose the training provider
                    </div>
                    <Button
                      onClick={() =>
                        this.setState({ open_select_development_partner: true })
                      }
                      theme="accent"
                      className="my-3"
                    >
                      Choose Training provider
                    </Button>
                  </div>
                </div>
              )}
              {this.state.action === "create" &&
                this.state.selected_development_partner !== null && (
                  <div>
                    <CreateAccount
                      developmentPartner={
                        this.state.selected_development_partner
                      }
                      onCancel={() => {
                        this.setState({
                          action:
                            this.state.action === "create" ? "list" : "create",
                        });
                      }}
                      onSuccess={(development_partner_id: string) =>
                        this.FC_GetAccounts(development_partner_id)
                      }
                    />
                  </div>
                )}
              {this.state.action === "list" &&
                this.state.selected_development_partner !== null && (
                  <div>
                    <DPAccountsList
                      selected_development_partner={
                        this.state.selected_development_partner
                      }
                      onCancel={() => {
                        this.setState({
                          action:
                            this.state.action === "create" ? "list" : "create",
                        });
                      }}
                      FC_GetAccounts={this.FC_GetAccounts}
                      accounts={this.state.accounts}
                      onAddNewAccount={() =>
                        this.setState({ action: "create" })
                      }
                      loading={this.state.loading}
                    />
                  </div>
                )}
            </div>
          )}
        </div>
      </div>
    );
  }
}

const mapStateToProps = ({ basic }: StoreState): { basic: Basic } => {
  return { basic: basic };
};

export const DPAccount = connect(mapStateToProps, { FC_GetAllSystemRoles })(
  _DPAccount
);
